<template>
    <v-container>
        <v-row class="mt-5" justify="center">
            <v-col cols="6">
                <h2>Редактирование новости</h2>
                <v-form v-if="article">
                    <v-text-field
                            label="Заголовок"
                            v-model="article.title"
                            :rules="[v => !!v || 'Обязательное поле']"
                            required
                    ></v-text-field>
                    <v-textarea
                            v-model="article.text"
                            label="Текст"
                    ></v-textarea>
                    <img v-if="!show_upload_image" class="image" :src="genImageLink()" alt="">
                    <img v-if="show_upload_image" class="image" :src="getUploadImageLink()" alt="">
                    <v-btn v-if="!load_new_image" @click="load_new_image = true" class="primary mt-5">Загрузить новое
                        изображение
                    </v-btn>
                    <v-file-input
                            v-if="load_new_image"
                            v-model="image"
                            :rules="rules"
                            accept="image/png, image/jpeg, image/bmp"
                            placeholder="Выбрать фото"
                            prepend-icon="mdi-camera"
                            label="Изображение"
                            class="file mt-5"
                            @change="showUploadImage"
                            outlined
                            name="image"
                    ></v-file-input>
                    <div class="mt-5">
                        <v-btn @click="submit" :disabled="!article.title || !article.text || uploading"
                               class="primary">Обновить новость
                        </v-btn>
                        <v-btn @click="dialog = true" class="secondary ml-5">
                            Удалить новость
                        </v-btn>
                    </div>
                </v-form>
            </v-col>
        </v-row>

        <v-dialog
                v-if="dialog"
                v-model="dialog"
                width="500"
        >

            <v-card>
                <v-card-title
                        class="headline grey lighten-2"
                        primary-title
                >
                    Удаление новости
                </v-card-title>

                <v-card-text>
                    <p>Вы действительно хотите удалить новость?</p>
                </v-card-text>

                <v-divider></v-divider>

                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn
                            color="primary"
                            text
                            @click="remove"
                    >
                        Да
                    </v-btn>
                    <v-btn
                            color="primary"
                            text
                            @click="dialog = false"
                    >
                        нет
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
    </v-container>
</template>

<script>
    import {mapActions} from 'vuex'

    export default {
        name: "EditArticle",
        data: () => ({
            dialog: false,
            uploading: false,
            load_new_image: false,
            show_upload_image: false,
            image: null,
            article: null,
            rules: [
                value => !value || value.size < 2000000 || 'Размер изображения не должен превышать 2 MB!',
            ],
        }),
        methods: {
            ...mapActions(['setAlert']),
            getUploadImageLink() {
                return URL.createObjectURL(this.image)
            },
            showUploadImage() {
                this.show_upload_image = true
            },
            async getArticle() {
                const res = await this.axios({
                    url: `${process.env.VUE_APP_SERVER_URL}api/v1/news/${this.$route.params.id}`,
                    method: 'GET'
                })
                this.article = res.data
            },
            genImageLink() {
                return `${process.env.VUE_APP_SERVER_URL}${this.article.image}`
            },
            submit() {
                this.uploading = true
                const formData = new FormData()
                if (this.image) formData.append('image', this.image)
                formData.append('title', this.article.title)
                formData.append('text', this.article.text)
                this.axios({
                    url: `${process.env.VUE_APP_SERVER_URL}api/v1/news/${this.$route.params.id}`,
                    method: 'PUT',
                    data: formData,
                    headers: {
                        'Content-Type': 'multipart/form-data'
                    }
                }).then(() => {
                    this.setAlert({
                        active: true,
                        color: 'success',
                        text: 'Новость обновлена'
                    })
                    this.uploading = false
                }).catch(err => {
                    this.setAlert({
                        active: true,
                        color: 'error',
                        text: err.response.data
                    })
                    this.uploading = false
                })
            },
            remove() {
                this.axios({
                    url: `${process.env.VUE_APP_SERVER_URL}api/v1/news/${this.$route.params.id}`, method: 'DELETE'
                }).then(() => {
                    this.setAlert({
                        active: true,
                        color: 'success',
                        text: 'Новость удалена'
                    })
                    this.$router.push('/news')
                }).catch(err => {
                    this.setAlert({
                        active: true,
                        color: 'error',
                        text: err.response.data
                    })
                })
            },
        },
        mounted() {
            this.getArticle()
        }
    }
</script>

<style scoped>
    .image {
        max-width: 100%;
    }
</style>